import { useState } from 'react';
import { Button, Form, Navbar, Table, Modal, FormControl, FormGroup } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { supplySlice } from './supplySlice';

const SupplyButton = (props) => {
  const [show, setShow] = useState(false);
  const [amount, setamount] = useState(0)

  const onAmountChange = e => setamount(e.target.value)

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const balance = props.walletAsset.balance
  const isConfirmDisabled = !!!amount || amount === 0 || amount > balance

  

  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        Supply
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Supply {props.market.asset}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <FormGroup>
              <Form.Label>Amount</Form.Label>
              <FormControl type="text" placeholder="Amount" value={amount} onChange={onAmountChange} />
            </FormGroup>
            <FormGroup>
              <Button>MAX</Button>
            </FormGroup>
          </Form>
          <Table>
            <tbody>
              <tr>
                <td>Balance</td>
                <td>{balance}</td>
              </tr>
            </tbody>
          </Table>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
          <Button variant="primary" disabled={isConfirmDisabled} onClick={handleClose}>
            Confirm
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

const SupplyMarket = () => {
  const supply = useSelector(state => state.supply)
  const wallet = useSelector(state => state.wallet)
  const dispatch = useDispatch()

  const onToggleChanged = (asset, value) => {
    const payload = {
      asset,
      value
    }
    dispatch(supplySlice.actions.toggleSupply(payload))
  }

  const getWalletAsset = (assetName) => wallet.assets.find(a => a.name == assetName)

  let marketRows = supply.markets.map(mkt => {
    const walletAsset = getWalletAsset(mkt.asset);
    const balance = walletAsset ? walletAsset.balance : 0
    return (
      <tr>
        <td>{mkt.asset}</td>
        <td>{mkt.APY}%</td>
        <td>{balance} {mkt.asset}</td>
        <td>
          <Form>
            <Form.Check
              type="switch"
              id={"switch"+mkt.asset}
              label=""
              onChange={e => onToggleChanged(mkt.asset, e.target.value)}
              value={mkt.isOn}
            ></Form.Check>
          </Form>
        </td>
        <td>
          {balance > 0 && <SupplyButton market={mkt} walletAsset={walletAsset} />}
          
        </td>
      </tr>
    )
  })
  return (
    <>
      <div 
        className="d-flex justify-content-center align-items-center"
        style={{
          height: "100px"
        }}
      >
        <span className="border border-primary p-2">Balance {supply.totalBalance}</span>
      </div>
      <h4>Supply Markets</h4>
      <Table>
        <thead>
          <th>Asset</th>
          <th>APY</th>
          <th>Wallet</th>
          <th>Collateral</th>
          <th></th>
        </thead>
        <tbody>
          {marketRows}
        </tbody>
      </Table>
    </>
  )
}

export default SupplyMarket